@import url(https://fonts.googleapis.com/css2?family=Poiret+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --largeurNavigateur: 900px;
    --largeurMobile: 350px;
}

.AppSiteUA{
    width: 99.5vw;
    overflow: hidden;
}

.Pages{
    background-color: white;
    display: grid;
    grid-template-areas: "a b c";
}

.LeftColumn, .RightColumn{
    background-color: #f2f2f2;
}
.LeftColumn{grid-area: a;}
.MiddleColumn{grid-area: b;}
.RightColumn{grid-area: c;}

#ulMenuNavigation {
    list-style: none;
    background-color: rgba(19, 67, 91, 0);
    position: fixed;
    z-index: 10;
    transition-property: background-color;
    transition-duration: 0.5s;
}

#liMenuNavigationIDlogo {
    grid-area: a;
    transition-property: opacity;
    transition-duration: 0.5s;}
#liMenuNavigationID {grid-area: c;}
#liMenuNavigationIDVieAssociative{grid-area: d;}
#liMenuNavigationIDManifestationAssociative{grid-area: e;}

.liMenuNavigation a
{
    text-decoration: none;
    color: white;
    font-family: "Poiret One", serif;
}

@media only screen and (max-width: 767px) {
    #BurgerButtonMenu{
        position: fixed;
        top: 17px;
        right: 17px;
        z-index: 11;
    }

    .BarBurgerButtonMenu{
        background-color: white;
        height: 3px;
        margin-bottom: 4px;
        width: 20px;
    }

    #uaLogoMenu{
        height: 30px;
        margin: 10px;
        opacity: 0;
        transition-property: opacity;
        transition-duration: 0.5s;
    }

    .Pages{
        grid-template-columns: calc((100vw - 350px)/2) 350px calc((100vw - 350px)/2);
        grid-template-columns: calc((100vw - var(--largeurMobile))/2) var(--largeurMobile) calc((100vw - var(--largeurMobile))/2);
    }

    #MobileHorizontalBarMenu{
        position: fixed;
        z-index: 11;
        top:0;
        left:0;
        background-color: rgba(19, 67, 91, 0);
        width: 100vw;
        height: 50px;
        transition-property: background-color;
        transition-duration: 0.5s;
    }

    #ulMenuNavigation {
        position: fixed;
        z-index: 11;
        top:50px;
        right: 0;
        border-top-color: rgba(255, 255, 255, 0.7);
        border-top-width: 1px;
        border-top-style: solid;
        margin-top:0;
        background-color: rgba(19, 67, 91, 0.7);
        padding: 10px;
    }

}

@media only screen and (min-width: 768px) {
    #uaLogoMenu{
        height: 50px;
    }

    #ulMenuNavigation {
        display: grid;
        grid-template-areas: "a b c d e";
        grid-template-columns: auto 150px auto auto auto;
        grid-gap: 50px;
        justify-content: center;
        justify-items: center;
        align-items: center;
        padding: 10px;
        width: 99.5vw;
        height: 50px;
        top:-18px;
        left:0;
    }

    .Pages{
        grid-template-columns: calc((99.5vw - 900px)/2) 900px calc((99.5vw - 900px)/2);
        grid-template-columns: calc((99.5vw - var(--largeurNavigateur))/2) var(--largeurNavigateur) calc((99.5vw - var(--largeurNavigateur))/2);
    }

    #liMenuNavigationIDlogo {
        transition-property: opacity;
        transition-duration: 0.5s;}
}

/* width */
::-webkit-scrollbar {
    width: 0.5vw;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
#SplashPage-h1{
    font-family: "Poiret One", serif;
    color: white;
    text-align: center;
}

#SplashPage-Container{
    background-color: rgba(19, 67, 91, 1);
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
